"use client";
import { GroupServices } from "@/app/(entities)/services/types";
import AccordionDetails from "@mui/material/AccordionDetails";
import AccordionSummary from "@mui/material/AccordionSummary";
import Typography from "@mui/material/Typography";
import { useState } from "react";
import FlexColumn from "../Layout/FlexColumn";
import PrimaryButton from "../Buttons/PrimaryButton";
import { StyledAccordion } from "./styled";
import FlexCenter from "../Layout/FlexCenter";
import Divider from "@mui/material/Divider";

type AccordionServicesProps = {
  servicesGroups: GroupServices[];
};

export default function AccordionServices({
  servicesGroups,
}: AccordionServicesProps) {
  const [expanded, setExpanded] = useState<string | false>(
    servicesGroups[0].id
  );

  const handleChange =
    (panel: string) => (event: React.SyntheticEvent, newExpanded: boolean) => {
      setExpanded(newExpanded ? panel : false);
    };

  return (
    <FlexColumn rowGap={"1rem"}>
      {servicesGroups?.map((group) => (
        <StyledAccordion
          expanded={expanded === group.id}
          onClick={() => handleChange(group.id)}
        >
          <AccordionSummary aria-controls={group.id} id={group.id}>
            <Typography variant="bodyXLSemiBold">{group.name}</Typography>
          </AccordionSummary>
          <Divider />
          <AccordionDetails>
            {group.services.map((service) => (
              <FlexCenter justifyContent={"space-between"}>
                <Typography variant="bodyLRegular">{service.name}</Typography>
                <Typography variant="bodySRegular">
                  {service.timestamp}
                </Typography>
                <Typography variant="bodyMSemiBold">
                  ${Number(service.price).toFixed()}
                </Typography>
                <PrimaryButton title={"Book"} size="small" />
              </FlexCenter>
            ))}
          </AccordionDetails>
        </StyledAccordion>
      ))}
    </FlexColumn>
  );
}
